@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
    font-family: 'Poppins';
    margin: 0;
    padding: 0;
    box-sizing: border-box; /* Ensures padding and border are included in element's total width and height */
}

body {
    background: linear-gradient(120deg, #ff7e5f, #feb47b);
    height: 150vh; /* Ensure body covers the entire viewport height */
}

.about {
    font-family: 'Poppins';
}

.about-content {
    max-width: 800px;
    margin: 0 auto;
    text-align: center;
    padding: 20px;
}

.about h1 {
    font-size: 2.5em;
    margin-bottom: 20px;
}

.about p {
    font-size: 1.2em;
    margin-bottom: 20px;
}

.about-info {
    text-align: left;
}

.about-info h2 {
    font-size: 2em;
    margin-bottom: 10px;
}

.about-info ul {
    list-style-type: disc;
    padding-left: 20px;
}

.about-info ul li {
    font-size: 1.1em;
    margin-bottom: 10px;
}

.aboutusimg img {
    max-width: 200px;
    border-radius: 15px;
    margin-right: 10px;
}
